/*=============== Header ====================================================================================================================================================================*/
.header-wrapper {
  position: relative;
  z-index: 100;
  background-color: transparent;
}

.header-inner {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  min-height: 60px;
}
.header-inner__logo {
  transition: transform 0.3s ease 0s;
}
.header-inner__logo a {
  text-decoration: none;
}
.header-inner__logo svg {
  max-width: 180px;
  width: 100%;
}
.header-inner__logo:hover {
  transform: scale(1.05);
}
.header-inner__logo:active {
  transform: scale(0.95);
}

/*=============== /Header ====================================================================================================================================================================*/
/*=============== Footer ====================================================================================================================================================================*/
.footer-wrapper {
  position: relative;
}

.footer-bg .shadow-bottom {
  z-index: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: bottom/100% 100% repeat-x;
}

.footer-inner {
  padding-bottom: 50px;
  position: relative;
}
.footer-inner__text {
  position: relative;
  color: #fff;
  text-align: center;
  font-size: 14px;
  line-height: 18px;
}
.footer-inner__text p {
  padding: 10px 0;
}
.footer-inner .firefly-bottom {
  pointer-events: none;
  position: absolute;
  z-index: 0;
  left: -10px;
  bottom: -10px;
  animation: firefly 1.2s 0s ease-in-out infinite alternate;
}
.footer-inner .firefly-bottom img {
  transform: scaleX(-1);
  opacity: 0.7;
  width: 130px;
}

.footer-links {
  padding: 0;
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  align-items: center;
}
.footer-links__item:not(:last-child):after {
  content: "|";
  color: #fff;
}
.footer-links__item a {
  padding: 0 5px;
  color: #fff;
  font-size: 14px;
}

/*=============== /Footer ====================================================================================================================================================================*/